.Helpers {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    padding: var(--large-padding);
    padding-left: 0;
    overflow: auto;
    gap: var(--large-padding);
}

.Helpers p {
    margin-bottom: 5px;
}

.Helpers .theme {
    border: solid 1px var(--color-one);
    border-radius: 30px;
    cursor: pointer;
    padding: 5px 10px;
}

.Helpers .theme p {
    margin: 0;
}