.Sidebar {
    border-right: solid 1px var(--color-one);
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    padding: var(--large-padding);
}

.Sidebar>div {
    cursor: pointer;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
}

.Sidebar>div:hover {
    box-shadow: var(--small-shadow);
    transition: 0.3s;
}