@import url(https://fonts.googleapis.com/css2?family=Lora&family=Nunito+Sans:wght@300&family=Source+Code+Pro&display=swap);
:root {
  --color-background: #ffffff;
  --color-dark: #000000;
  --color-light: #ffffff;
  --color-one: #AEB8FE;
  --color-two: #758BFD;
  --hover-color: #ffffff;
  --large-padding: 30px;
  --medium-padding: 20px;
  --small-shadow: 0px 0px 13px rgba(0, 0, 0, 0.11);
  --large-shadow: 0px 32px 53px rgba(17, 17, 17, 0.08);
}

::-webkit-scrollbar {
  width: 0;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

body, html {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
  background-color: var(--color-background);
  height: 100%;
  color: #000000;
  color: var(--color-dark);
  overflow: hidden;
}

body>#root {
  height: 100%;
}

a {
  color: #758BFD;
  color: var(--color-two);
}

h1 {
  font-family: 'Lora', serif;
  font-size: 64px;
  font-weight: 300;
  margin: 0;
}

h2 {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 36px;
  margin: 0;
}

p, pre {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 16px;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    display: inline-block;
    background-color: #000000;
    background-color: var(--color-dark);
    color: #ffffff;
    color: var(--color-light);
    padding: 10px;
    border-radius: 5px;
    border-left: #758BFD solid 3px;
    border-left: var(--color-two) solid 3px;
    font-size: 16px;
}

.App {
  display: grid;
  grid-template-columns: 90px 1fr 360px;
  height: 100%;
}
.Sidebar {
    border-right: solid 1px var(--color-one);
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 30px;
    gap: 30px;
    padding: var(--large-padding);
}

.Sidebar>div {
    cursor: pointer;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
}

.Sidebar>div:hover {
    box-shadow: var(--small-shadow);
    transition: 0.3s;
}
.Activity {
    border-radius: 5px;
    border: solid 1px var(--color-one);
    padding: var(--medium-padding);
    box-sizing: border-box;
    transition: 0.3s;
}

.Activity:hover {
    box-shadow: var(--large-shadow);
    transition: 0.3s;
    cursor: pointer;
    background-color: var(--hover-color);
}
.Question {

}

.Question .title {
    
}

.Question .content {
    border-left: solid 2px var(--color-dark);
    padding-left: var(--medium-padding);
}

.Question .content>div {
    padding: 10px;
}

.Question .content p {
    margin-bottom: 5px;
}

.Question .content .reveal {
    color: var(--color-two);
    cursor: pointer;
}
.Main {
    padding: 30px;
    overflow: auto;
}

.Main main {
    display: flex;
    flex-direction: column;
    grid-gap: var(--large-padding);
    gap: var(--large-padding);
}

.Main header {
    padding-bottom: var(--medium-padding);
}
.Helper {
    border: solid 1px var(--color-one);
    border-radius: 5px;
    padding: var(--medium-padding);
    box-sizing: border-box;
    width: 100%;
}
.Helpers {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    padding: var(--large-padding);
    padding-left: 0;
    overflow: auto;
    grid-gap: var(--large-padding);
    gap: var(--large-padding);
}

.Helpers p {
    margin-bottom: 5px;
}

.Helpers .theme {
    border: solid 1px var(--color-one);
    border-radius: 30px;
    cursor: pointer;
    padding: 5px 10px;
}

.Helpers .theme p {
    margin: 0;
}
