@import url('https://fonts.googleapis.com/css2?family=Lora&family=Nunito+Sans:wght@300&family=Source+Code+Pro&display=swap');

:root {
  --color-background: #ffffff;
  --color-dark: #000000;
  --color-light: #ffffff;
  --color-one: #AEB8FE;
  --color-two: #758BFD;
  --hover-color: #ffffff;
  --large-padding: 30px;
  --medium-padding: 20px;
  --small-shadow: 0px 0px 13px rgba(0, 0, 0, 0.11);
  --large-shadow: 0px 32px 53px rgba(17, 17, 17, 0.08);
}

::-webkit-scrollbar {
  width: 0;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

body, html {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-background);
  height: 100%;
  color: var(--color-dark);
  overflow: hidden;
}

body>#root {
  height: 100%;
}

a {
  color: var(--color-two);
}

h1 {
  font-family: 'Lora', serif;
  font-size: 64px;
  font-weight: 300;
  margin: 0;
}

h2 {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 36px;
  margin: 0;
}

p, pre {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 16px;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    display: inline-block;
    background-color: var(--color-dark);
    color: var(--color-light);
    padding: 10px;
    border-radius: 5px;
    border-left: var(--color-two) solid 3px;
    font-size: 16px;
}
