.Activity {
    border-radius: 5px;
    border: solid 1px var(--color-one);
    padding: var(--medium-padding);
    box-sizing: border-box;
    transition: 0.3s;
}

.Activity:hover {
    box-shadow: var(--large-shadow);
    transition: 0.3s;
    cursor: pointer;
    background-color: var(--hover-color);
}